<template>
  <div class="test padding-top">
    <ul class="test-ul">
      <li v-for="(item,index) in list" :key="index">
        <cube-button @click="item.fn">{{item.btnName}}</cube-button>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "test",
  components: {},
  data() {
    let that = this;
    return {
      hasBridge: false,
      list: [
        {
          fn() {
            that.$call("19999999999").then(res => {
              that.$dialog({
                type: "alert",
                title: "打电话回调",
                content: JSON.stringify(res)
              });
            });
          },
          btnName: "打电话"
        },
        {
          fn() {
            that.$getCommonParams().then(res => {
              that.$dialog({
                type: "alert",
                title: "获取app通用参数回调",
                content: JSON.stringify(res)
              });
            });
          },
          btnName: "获取app通用参数"
        },
        {
          fn() {
            that.$sendToken("token20190517").then(res => {
              that.$dialog({
                type: "alert",
                title: "发送token给客户端回调",
                content: ""
              });
            });
          },
          btnName: "发送token给客户端"
        },
        {
          fn() {
            that.$update().then(res => {
              that.$dialog({
                type: "alert",
                title: "版本更新",
                content: ""
              });
            });
          },
          btnName: "版本更新"
        },
        {
          fn() {
            let options = {
              title: "百度",
              content: "我是百度",
              link: "https://www.baidu.com/",
              picture:
                "https://cdn2.jianshu.io/assets/default_avatar/4-3397163ecdb3855a0a4139c34a695885.jpg?imageMogr2/auto-orient/strip|imageView2/1/w/96/h/96"
            };
            that.$share(options).then(res => {
              that.$dialog({
                type: "alert",
                title: "分享",
                content: ""
              });
            });
          },
          btnName: "分享"
        },
        {
          fn() {
            that.$clearCache().then(res => {
              that.$dialog({
                type: "alert",
                title: "清理缓存",
                content: ""
              });
            });
          },
          btnName: "清理缓存"
        },
        {
          fn() {
            that.$getLocation().then(res => {
              that.$dialog({
                type: "alert",
                title: "获取位置",
                content: JSON.stringify(res)
              });
            });
          },
          btnName: "获取位置"
        },
        {
          fn() {
            that.$getContacts().then(res => {
              that.$dialog({
                type: "alert",
                title: "读取通讯录",
                content: JSON.stringify(res)
              });
            });
          },
          btnName: "读取通讯录"
        },
        {
          fn() {
            that.$getCallRecords().then(res => {
              that.$dialog({
                type: "alert",
                title: "读取通话纪录",
                content: JSON.stringify(res)
              });
            });
          },
          btnName: "读取通话纪录"
        }
      ]
    };
  },
  methods: {},
  mounted: function() {}
};
</script>
<style lang="stylus">
.test {
  > p {
    padding: 0 0.2rem;
    line-height: 0.5rem;
  }

  ul {
    display: flex;
    flex-wrap: wrap;

    li {
      width: 45%;
      margin: 0.1rem 2.5%;

      button {
        width: 100%;
      }
    }
  }
}
</style>